import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Card} from '@/modules/contract/cards';
import {RoundBasedGame, RoundStatus} from '@/modules/contract/roundBasedGame/roundBasedGame';

export enum RedBlackBetType {
    Unknown = 0, RedWin = 1, BlackWin = 2, Pair = 4, Straight = 8, Flush = 16, StraightFlush = 32, ThreeOfAKind = 64,
}

export class RedBlackGame extends RoundBasedGame {
    public static betTypeToString(betType: RedBlackBetType): string {
        switch (betType) {
            case RedBlackBetType.RedWin:
                return 'Red Win';
            case RedBlackBetType.BlackWin:
                return 'Black Win';
            case RedBlackBetType.Pair:
                return 'Pair';
            case RedBlackBetType.Straight:
                return 'Straight';
            case RedBlackBetType.Flush:
                return 'Flush';
            case RedBlackBetType.StraightFlush:
                return 'Straight Flush';
            case RedBlackBetType.ThreeOfAKind:
                return 'Three-of-a-Kind';
            default:
                return '---';
        }
    }

    public readonly redCards: Card[];
    public readonly blackCards: Card[];
    public readonly redHandType: RedBlackBetType;
    public readonly blackHandType: RedBlackBetType;
    public readonly result: RedBlackBetType;

    public constructor(value: any) {
        super(value);
        if (this.status === RoundStatus.StandBy) {
            this.redCards = value.red_cards.map((card: number) => new Card(card, true));
            this.blackCards = value.black_cards.map((card: number) => new Card(card, true));
        } else {
            this.redCards = [];
            this.blackCards = [];
        }

        if (this.redCards.length === 3 && this.blackCards.length === 3) {
            this.redCards.sort((a, b) => a.value - b.value);
            this.blackCards.sort((a, b) => a.value - b.value);

            this.redHandType = this.getHandType(this.redCards);
            this.blackHandType = this.getHandType(this.blackCards);
            this.result = this.getGameResult();
        } else {
            this.result = RedBlackBetType.Unknown;
            this.redHandType = RedBlackBetType.Unknown;
            this.blackHandType = RedBlackBetType.Unknown;
        }
    }

    public getResultString(): string {
        return RedBlackGame.betTypeToString(this.result);
    }

    public getRedHandTypeString(): string {
        return RedBlackGame.betTypeToString(this.redHandType);
    }

    public getBlackHandTypeString(): string {
        return RedBlackGame.betTypeToString(this.blackHandType);
    }

    private compareForResult(red: number, black: number) {
        if (red > black) {
            return RedBlackBetType.RedWin;
        } else if (black > red) {
            return RedBlackBetType.BlackWin;
        } else {
            return RedBlackBetType.Unknown;
        }
    }

    private getGameResult(): RedBlackBetType {
        let result: RedBlackBetType = this.compareForResult(this.redHandType, this.blackHandType);
        if (result === RedBlackBetType.Unknown) {
            switch (this.redHandType) {
                case RedBlackBetType.Straight:
                case RedBlackBetType.StraightFlush:
                case RedBlackBetType.ThreeOfAKind:
                    result = this.compareForResult(this.redCards[1].value, this.blackCards[1].value);
                    break;
                case RedBlackBetType.Pair:
                    result = this.compareForResult(this.redCards[1].value, this.blackCards[1].value);
                    if (result === RedBlackBetType.Unknown) {
                        result = this.compareForResult(
                                this.redCards[0].value === this.redCards[1].value ?
                                        this.redCards[2].value : this.redCards[0].value,
                                this.blackCards[0].value === this.blackCards[1].value ?
                                        this.blackCards[2].value : this.blackCards[0].value,
                        );
                    }
                    break;
                default:
                    for (let i = 2; i >= 0; i--) {
                        result = this.compareForResult(this.redCards[i].value, this.blackCards[i].value);
                        if (result !== RedBlackBetType.Unknown) {
                            break;
                        }
                    }
            }
        }
        return result;
    }

    private getHandType(cards: Card[]): RedBlackBetType {
        const isSameSuit: boolean = cards[0].suit === cards[1].suit && cards[1].suit === cards[2].suit;

        let straight: boolean = true;
        let threeOfAKind: boolean = true;
        let pair: boolean = false;
        let lastValue: number = cards[0].value;

        for (let i = 1; i < 3; i++) {
            const value: number = cards[i].value;
            if (value === lastValue) {
                pair = true;
            } else {
                threeOfAKind = false;
            }
            if (value - lastValue !== 1 && (value !== 13 || lastValue !== 3)) {
                straight = false;
            }
            lastValue = value;
        }

        if (straight && cards[0].value === 2 && cards[2].isAce()) {
            cards.unshift(cards.pop()!);
        }

        if (threeOfAKind) {
            return RedBlackBetType.ThreeOfAKind;
        } else if (straight) {
            return isSameSuit ? RedBlackBetType.StraightFlush : RedBlackBetType.Straight;
        } else if (isSameSuit) {
            return RedBlackBetType.Flush;
        } else if (pair) {
            return RedBlackBetType.Pair;
        } else {
            return RedBlackBetType.Unknown;
        }
    }
}

export class RedBlackGameTable extends Table<RedBlackGame> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'games', 'symbol', contract);
    }

    protected convert(row: any[]): RedBlackGame[] {
        return row.map((value) => new RedBlackGame(value));
    }
}
