import {Asset} from '@/modules/eos/asset';
import {Account} from '@/modules/eos/account';
import {isPresent} from '@/modules/utils/commonFunctions';

export interface Limit {
    readonly used: number;
    readonly available: number;
    readonly max: number;
}

export class AccountInfo {
    public static DEFAULT: AccountInfo = new AccountInfo(Account.DEFAULT, undefined);
    private static EMPTY_LIMIT: Limit = AccountInfo.makeLimit(0, 0);
    private static makeLimit(used: number, max: number): Limit {
        return {used, max, available: max - used};
    }

    public readonly balance: Asset;
    public readonly cpuLimit: Limit;
    public readonly netLimit: Limit;
    public readonly ramLimit: Limit;

    public constructor(public readonly account: Account,
                       public info: any) {
        if (isPresent(info)) {
            this.balance = Asset.fromString(info.core_liquid_balance);
            this.cpuLimit = info.cpu_limit;
            this.netLimit = info.net_limit;
            this.ramLimit = AccountInfo.makeLimit(info.ram_usage, info.ram_quota);
        } else {
            this.balance = Asset.DEFAULT;
            this.cpuLimit = AccountInfo.EMPTY_LIMIT;
            this.netLimit = AccountInfo.EMPTY_LIMIT;
            this.ramLimit = AccountInfo.EMPTY_LIMIT;
        }
    }
}
