import {EOS} from '../eos/eos';
import {isBlank, isPresent} from '../utils/commonFunctions';
import {AccountInfo} from '../eos/accountInfo';

export class AccountInfoControl {
    public static getInstance(eos: EOS): AccountInfoControl {
        if (isBlank(AccountInfoControl.instance)) {
            this.instance = new AccountInfoControl(eos);
        }
        return this.instance!;
    }

    private static UPDATE_FREQUENCY: number = 5000;
    private static MAX_RETRY: number = 5;
    private static instance?: AccountInfoControl;

    public accountInfo: AccountInfo = AccountInfo.DEFAULT;
    private ticker: any = undefined;

    private constructor(private eos: EOS) {
    }

    public async ensureUpToDate(): Promise<any> {
        this.stopUpdate();

        let retryCount = 0;
        while (retryCount < AccountInfoControl.MAX_RETRY) {
            try {
                await this.update();
                break;
            } catch (e) {
                retryCount ++;
            }
        }
        this.startUpdate();
    }

    public adjustBalance(amount: number): void {
        this.accountInfo.balance.add(amount);
    }

    public async startUpdate(): Promise<any> {
        if (isBlank(this.ticker)) {
            this.ticker = setInterval(() => {
                this.update();
            }, AccountInfoControl.UPDATE_FREQUENCY);
        }
    }

    public stopUpdate(): void {
        if (isPresent(this.ticker)) {
            clearInterval(this.ticker);
            this.ticker = undefined;
        }
    }

    private async update(): Promise<any> {
        this.accountInfo = await this.eos.getAccountInfo();
    }
}
