import {BetCallback, BetController} from '@/modules/contract/betController';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {EOS} from '@/modules/eos/eos';
import {AccountInfoControl} from '@/modules/contract/accountInfoControl';

export abstract class GameController implements BetCallback {
    public readonly house: HouseContract;
    public readonly accountControl: AccountInfoControl;
    public readonly betControl: BetController;

    protected constructor(public readonly eos: EOS) {
        this.house = new HouseContract(eos);
        this.accountControl = AccountInfoControl.getInstance(eos);
        this.betControl = new BetController(eos, this, this.accountControl);

        this.accountControl.ensureUpToDate();
    }

    public abstract betText(): string;
    public abstract canMakeBet(): boolean;
    public abstract makeBet(amount: number): Promise<any>;

}
