import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {BaccaratBetType, BaccaratGame} from '@/modules/contract/baccarat/baccaratGameTable';
import {Bet} from '@/modules/contract/roundBasedGame/bet';

export class BaccaratBet extends Bet<BaccaratBetType> {
    public getBetType(): string {
        return BaccaratGame.betTypeToString(this.betType);
    }
}

export class BaccaratBetTable extends Table<BaccaratBet> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'bets', 'id', contract);
    }

    protected convert(row: any[]): BaccaratBet[] {
        return row.map((value) => new BaccaratBet(value));
    }
}
