import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Bet} from '@/modules/contract/roundBasedGame/bet';
import {RedBlackBetType, RedBlackGame} from '@/modules/contract/redblack/redBlackGameTable';

export class RedBlackBet extends Bet<RedBlackBetType> {
    public getBetType(): string {
        return RedBlackGame.betTypeToString(this.betType);
    }
}

export class RedBlackBetTable extends Table<RedBlackBet> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'bets', 'id', contract);
    }

    protected convert(row: any[]): RedBlackBet[] {
        return row.map((value) => new RedBlackBet(value));
    }
}
