import {Asset} from '../../eos/asset';

export abstract class Bet<T> {
    public readonly id: number;
    public readonly gameId: number;
    public readonly player: string;
    public readonly referer: string;
    public readonly bet: Asset;
    public readonly betType: T;

    public constructor(value: any) {
        this.id = value.id;
        this.gameId = value.game_id;

        this.player = value.player;
        this.referer = value.referer;
        this.bet = Asset.fromString(value.bet);
        this.betType = value.bet_type;
    }

    public abstract getBetType(): string;
}
