

















import {Component, Vue} from 'vue-property-decorator';
import {Scatter} from '@/modules/eos/scatter';
import {EOS} from '@/modules/eos/eos';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';
import {Constants} from '@/modules/eos/constants';
import {HouseContract} from '@/modules/contract/house/houseContract';
import {Asset} from '@/modules/eos/asset';
import {Token} from '@/modules/eos/token';
import {RedBlackContract} from '@/modules/contract/redblack/redBlackContract';

@Component
export default class FunctionTests extends Vue {
    public message: string = '';
    private eos = new EOS(new Scatter(Constants.NETWORK));
    private house: HouseContract = new HouseContract(EOS.instance);
    private current = new RedBlackContract(this.house);

    public async connect(): Promise<any> {
        if (this.eos.loggedIn()) {
            await this.eos.connector.logout();
        } else {
            await this.eos.connector.login();
            this.message = 'Hello ' + this.eos.connector.getAccount();
        }
    }

    public async getTokenTable(): Promise<any> {
        const rows: any[] = await this.current.tokens.readAll();
        this.message = JSON.stringify(rows);
    }

    public async getPlayerTable(): Promise<any> {
        const rows: any[] = await this.current.players.readAll();
        this.message = JSON.stringify(rows);
    }

    public async getGlobalTable(): Promise<any> {
        const rows: any[] = await this.current.globals.readAll();
        this.message = JSON.stringify(rows);
    }

    public async getGameTable(): Promise<any> {
        const rows: any[] = await this.current.gameTable.readAll();
        this.message = JSON.stringify(rows);
    }

    public async init(): Promise<any> {
        const result: any = await this.house.addGame(this.current.name, this.current.id);
        this.message = JSON.stringify(result);
    }

    public async close(): Promise<any> {
        const result: TransactionReceipt = await this.current.reveal(1);
        this.message = JSON.stringify(result);
    }

    public async transfer(): Promise<any> {
        const result = await this.eos.transfer(HouseContract.CONTRACT, new Asset(500, Token.EOS),
                'deposit,' + this.current.name);
        this.message = JSON.stringify(result);
    }
}


