import {isEmptyString} from '@/modules/utils/commonFunctions';

export class Account {
    public static readonly DEFAULT: Account = new Account('N/A', '');
    public readonly blockchain: string = 'eos';

    public constructor(public readonly name: string,
                       public readonly authority: string) {
    }

    public isValid(): boolean {
        return !isEmptyString(this.authority);
    }

    public toString(): string {
        if (isEmptyString(this.authority)) {
            return this.name;
        } else {
            return this.name + '@' + this.authority;
        }
    }
}
