import {Asset} from '../eos/asset';
import {Token} from '../eos/token';
import {EOS} from '../eos/eos';
import {isPresent} from '../utils/commonFunctions';
import {AccountInfoControl} from '@/modules/contract/accountInfoControl';

export interface BalanceEffectCallback {
    startBalanceEffect(amount: number, duration?: number): void;
}

export interface BetCallback {
    betText(): string;
    canMakeBet(): boolean;
    makeBet(amount: number): Promise<any>;
}

export class BetController {
    public static readonly MIN_BET: number = 0.1;
    public static readonly MAX_BET: number = 50;

    public amount: number = 1;
    public effectCallback?: BalanceEffectCallback = undefined;
    public isBetting: boolean = false;

    public constructor(private readonly eos: EOS,
                       private readonly betCallback: BetCallback,
                       public readonly accountInfoControl: AccountInfoControl) {
    }

    public setEffectCallback(effectCallback: BalanceEffectCallback): void {
        this.effectCallback = effectCallback;
    }

    public async makeBet(): Promise<any> {
        await this.eos.ensureConnected();

        this.isBetting = true;
        try {
            await this.accountInfoControl.ensureUpToDate();
            await this.betCallback.makeBet(this.amount);

            if (isPresent(this.effectCallback)) {
                const payAmount: number = -(this.amount);
                this.effectCallback!.startBalanceEffect(payAmount, 1000);
                this.accountInfoControl.adjustBalance(payAmount);
            }
        } catch (e) {
            this.isBetting = false;
            alert('Bet failed, make sure you have signed in to scatter and approved the transaction.');
        }
    }

    public async finishBet(profit: number): Promise<any> {
        this.isBetting = false;
        await this.accountInfoControl.ensureUpToDate();
        if (isPresent(this.effectCallback) && profit > 0) {
            this.effectCallback!.startBalanceEffect(profit);
        }
    }

    public getBalance(): Asset {
        return this.accountInfoControl.accountInfo.balance;
    }

    public getBetAsset(): Asset {
        return new Asset(this.amount, Token.EOS);
    }

    public canMakeBet(): boolean {
        return !this.isBetting && this.betCallback.canMakeBet();
    }

    public betText(): string {
        return this.betCallback.betText();
    }

    public stepAmount(times: number): void {
        this.amount = this.amount * times;
    }

    public setMin(): void {
        this.amount = BetController.MIN_BET;
    }

    public setMax(): void {
        this.amount = BetController.MAX_BET;
    }
}
