import {isPresent} from '../../utils/commonFunctions';

export enum RoundStatus {
    Unknown, StandBy, Active,
}

export abstract class RoundBasedGame {
    public readonly id: number;
    public readonly endTime: number;
    public readonly status: RoundStatus;
    public readonly symbol: string;

    public constructor(value: any) {
        if (isPresent(value)) {
            this.id = value.id;
            this.endTime = value.end_time;
            this.status = value.status === 1 ? RoundStatus.StandBy : RoundStatus.Active;
            this.symbol = value.symbol;
        } else {
            this.id = 0;
            this.endTime = 0;
            this.status = RoundStatus.Unknown;
            this.symbol = '';
        }
    }

    public abstract getResultString(): string;

    public canStartBet() {
        const timestamp: number = Date.now() / 1000;
        return (this.status === RoundStatus.StandBy && timestamp >= this.endTime) ||
                (this.status === RoundStatus.Active && timestamp < this.endTime);
    }

    public isActive(): boolean {
        return this.status === RoundStatus.Active;
    }
}
