import {EOS} from '../../eos/eos';
import {RedBlackBetType, RedBlackGame} from './redBlackGameTable';
import {RoundBasedGameController} from '@/modules/contract/roundBasedGame/roundBasedGameController';
import {RedBlackContract} from '@/modules/contract/redblack/redBlackContract';

export class RedBlackController extends RoundBasedGameController<RedBlackBetType> {
    public readonly contract: RedBlackContract;
    public game: RedBlackGame = new RedBlackGame(undefined);
    public betType: RedBlackBetType = RedBlackBetType.Unknown;

    public constructor(eos: EOS) {
        super(eos);
        this.contract = new RedBlackContract(this.house);
    }
}
