import {GameContract} from '../house/gameContract';
import {GlobalTable} from '../house/globalTable';
import {HouseContract} from '../house/houseContract';
import {TransactionReceipt} from '../../eos/transactionReceipt';
import {Asset} from '../../eos/asset';
import {Table} from '../../eos/table';
import {RoundBasedGame} from '@/modules/contract/roundBasedGame/roundBasedGame';
import {Bet} from '@/modules/contract/roundBasedGame/bet';

export abstract class RoundBasedGameContract<T> extends GameContract {
    public readonly globalVarTable: GlobalTable;
    public abstract readonly gameTable: Table<RoundBasedGame>;
    public abstract readonly betTable: Table<Bet<T>>;


    protected constructor(name: string, house: HouseContract, id: number) {
        super(name, house, id);

        this.globalVarTable = new GlobalTable(this.eos, this.name);
    }

    public init(): Promise<TransactionReceipt> {
        return this.pushAction('init', {});
    }

    public setGlobal(key: number, value: number): Promise<TransactionReceipt> {
        return this.pushAction('setglobal', {key, value});
    }

    public close(id: number): Promise<TransactionReceipt> {
        return this.pushAction('hardclose', {game_id: id});
    }

    public reveal(id: number): Promise<TransactionReceipt> {
        return this.pushAction('reveal', {game_id: id});
    }

    public play(gameId: number, betType: T, asset: Asset): Promise<TransactionReceipt> {
        return this.house.transfer(asset, this.id + ',' + gameId + ',' + betType + ',');
    }
}
